var React=require("react");
var Reflux=require("reflux");
var store=require("./store");

var Definitions=React.createClass({displayName: "Definitions",
	mixins:[Reflux.listenTo(store,"onStore")]
	,getInitialState:function() {
		return {def:"search and click on left panel"}
	}
	,onStore:function(def) {
		this.setState({def:def});
	}
	,render : function(){
		return React.createElement("div", null, this.state.def)
	}
})
module.exports=Definitions;